* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}

.app {
  background: url('./assets/cold.jpeg') no-repeat center;
  background-size: cover;
  transition: .2s ease-out
}

.app.warm {
  background: url('./assets/warm.jpeg') no-repeat center;
  background-size: cover;
  transition: .2s ease-out
}

main {
  min-height: 100vh;
  background: linear-gradient(to bottom, rgba(0,0,0,.3), rgba(0,0,0,.8));
  padding: 1.5rem 0;
}

.container {
  width: 90%;
  max-width: 500px;
  margin: 0 auto;
  padding: 2rem 1rem;
  background-color: rgba(80,80,80,.3);
  border-radius: 6px;
  backdrop-filter: blur(5px);
}

.main-heading {
  color: #fff;
  text-align: center;
  margin: 0 0 .4rem 0;
  text-shadow: 2px 4px rgba(50,50,70,.5);
  font-size: 1.8rem;
  font-weight: 300;
}

.ref-link {
  color: #fff;
  font-size: .8rem;
  text-align: center;
  margin-bottom: 1rem;
}

.ref-link a {
  color: rgb(170, 157, 83)
}

.search-box {
  width: 100%;
  margin: 0 0 2rem;
}

.search-bar {
  display: block;
  width: 100%;
  padding: .5rem;
  appearance: none;
  background: rgba(255,255,255,.3);
  border-radius: 5px;
  border: none;
  outline: none;
  color: #fff;
  font-size: 1.5rem;
  box-shadow: 0 6px 18px rgba(0,0,0,.2);
  transition: .2s ease;
}

.search-bar::placeholder {
  color: #bbb
}

.search-bar:focus {
  background: rgba(255,255,255,.4);
}

.location {
  color: #fff;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,.5);
}

.date {
  color: rgb(124, 188, 248);
  font-size: .9rem;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,.5);
}

.weather-box {
  text-align: center;
}

.temp-box {
  display: flex;
  justify-content: center;
  align-items: center;
}

.min-temp, .max-temp {
  padding: 0.2rem .5rem;
  border-radius: .5rem;
  color: #fff;
  font-size: .9rem;
  font-weight: 300;
  text-shadow: 2px 2px rgba(50,50,70,.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,.2);
  background-color: rgba(150,150,150,.7);
}

.current-temp {
  position: relative;
  display: inline-block;
  margin: 1rem auto;
  background-color: rgb(81, 66, 163);
  border-radius: .5rem;
  padding: .4rem .8rem;
  color: #fff;
  font-size: 3rem;
  font-weight: 800;
  text-shadow: 3px 6px rgba(50,50,70,.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,.2);
}

.weather p {
  color: #fff;
  font-size: 1rem;
  line-height: 140%;
  font-weight: 300;
  letter-spacing: 1px;
  text-shadow: 2px 3px rgba(50,50,70,.5);
}
.weather p span {
  opacity: .7;
}
.ref-link.bottom {
  margin-top: 2rem;
}
.ref-link.bottom a {
  color: rgb(245, 135, 116);
}
